.works {
	background-color: rgba(#FF6F61, 0.2);
	// margin: 227px auto
	padding: 227px 0 227px;
	overflow: hidden;
	&_recent {
 }		// min-height: 3000px
	&__title-wrapper {
		margin-bottom: 80px; }
	&__title {
		position: relative;
		margin-bottom: 30px;
		&_filter {
			span {
				display: inline-block !important; }
			&:after {
				content: '';
				display: inline-block;
				vertical-align: middle;
				@include arr(10, 6, $red, t);
				transition: opacity 0.8s 1s, transform 0.8s;
				opacity: 0;
				.is-animate & {
					opacity: 1; }
				.is-open & {
					transform: rotate(-180deg); } } } }
	&__container {
		position: relative;
		opacity: 0;
		visibility: hidden;
		transition: opacity $tr, visibility $tr;
		&.is-init {
			opacity: 1;
			visibility: visible; } }
	&__item {
		width: calc(50% - 60px);
		@include md {
			width: calc(50% - 50px); }
		&_padding {
			position: absolute;
			right: 0 !important;
			top: 0 !important;
			left: auto !important;
			height: 200px; } }
	&__filter-wrapper {
		margin-bottom: 82px; }
	&__filter {
		display: none;
		margin-top: 87px; }
	&__filter-btn {
		@extend .title;
		background-color: transparent; }
	&__btn {
		width: 100%;
		position: absolute;
		width: 100%;
		left: 0 !important;
		height: 30px;
		&:nth-child(even) {
			top: auto !important;
			bottom: 140px !important; }
		.anim-line {
			margin-right: 40px; } } }

.work {
	// width: 20%
	padding-bottom: 110px;
	&__image-wrap {
		overflow: hidden;
		transition: transform $tr;
		background-color: #ccc;
		backface-visibility: hidden;
		@include hover {
			transform: scale(0.97);
			.work__image_scale {
				transform: scale(1.1); } } }
	&__image {
		transition: transform $tr;
		img {
			max-width: 100%;
			// width: 100%
			display: block; } } }

.js-masonry-width {
	width: calc(50% - 60px);
	@include md {
		width: calc(50% - 50px); } }

.js-masonry-gutter {
	width: 120px;
	@include md {
		width: 100px; } }

.filter-controls {
	display: flex;
	align-items: center;
	&__item {
		@include flex-size(20%); } }

.anim-line {
	display: inline-block;
	vertical-align: middle;
	height: 1px;
	width: 40px;
	background-color: $greyMid; }
